<template>
    <div class="main-container">
        <div class="video-container">
            <video id="video" autoplay="autoplay"></video>
        </div>
        <div class="side-container" v-if="isChatClosed == false">
            <aside class="assid">
                <div id="menu">
                    <button class="button" id="member-list" type="button" @click="activate_button(0)">Members</button>
                    <button class="button" id="chat-view" type="button" @click="activate_button(1)">Chat</button>
                </div>
                <div class="toto" v-if="button_active == 0">
                    Members tab
                </div>
                <div class="tata" v-else>
                    Chat tab
                </div>
            </aside>
        </div>
    </div>
</template>
<script>
import "../assets/js/jquery.min"
import "../assets/js/adapter"
import "../assets/js/jquery.verto"
import "../assets/js/jquery.FSRTC"
import "../assets/js/jquery.jsonrpcclient"
import "../conference"

export default {
    name: 'Conference',
    props:
    {
        param: String,
    },
    data()
    {
        return {
            button_active: 0,
            isChatClosed: false
        }
    },
    methods:
    {
        activate_button: function(el)
        {
            this.button_active = el;
        },
    }
}
</script>
<style scoped>
#main-container
{
    width: 100%;
    height: 100%;
}
#video
{
	width: 80%;
    height: 100%;
	background: black;
    float: left;
}
#menu {
	height: 5%;
	overflow: hidden;
}
#menu button {
    float: left;
    border: none;
    border-bottom: 5px solid #ddd;
    outline: none;
    cursor: pointer;
    width: 50%;
	height: 100%;
    transition: 0.3s;
    background-color: white;
}
#menu button:hover {
    border-bottom: 5px solid #17a2b8;
    color: #17a2b8;
}
</style>
